import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useLanguage } from '../contexts/LanguageContext';
import { useAuth } from '../contexts/AuthContext';
import Logo from '../components/Logo';
import Footer from '../components/Footer';
import api from '../services/api';
import {
  Box,
  Container,
  Typography,
  Avatar,
  Card,
  CardContent,
  Grid,
  Button,
  IconButton,
  Chip,
  Divider,
  CircularProgress,
  TextField,
  MenuItem,
  Stack,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Alert,
  AppBar,
  Toolbar,
  FormControl,
  Select
} from '@mui/material';
import {
  ArrowBack,
  Facebook,
  Instagram,
  LocationOn,
  Phone,
  Email,
  Schedule,
  Person,
  CheckCircle,
  Favorite,
  ContentCut,
  Spa,
  FitnessCenter
} from '@mui/icons-material';

const BusinessDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { language, changeLanguage } = useLanguage();
  const { isAuthenticated } = useAuth();

  const [business, setBusiness] = useState(null);
  const [workers, setWorkers] = useState([]);
  const [services, setServices] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Booking dialog state
  const [bookingDialogOpen, setBookingDialogOpen] = useState(false);
  const [selectedService, setSelectedService] = useState(null);
  const [selectedWorker, setSelectedWorker] = useState('');
  const [selectedDate, setSelectedDate] = useState('');
  const [selectedTime, setSelectedTime] = useState('');
  const [bookingSuccess, setBookingSuccess] = useState(false);
  const [bookingError, setBookingError] = useState('');

  useEffect(() => {
    const fetchBusinessData = async () => {
      try {
        setLoading(true);
        setError(null);

        // Fetch business profile
        const businessResponse = await api.get(`/business/${id}`);
        setBusiness(businessResponse.data);

        // Fetch workers for this business
        try {
          const workersResponse = await api.get('/barbers/');
          // Filter workers that belong to this business (if backend provides business_id)
          setWorkers(workersResponse.data || []);
        } catch (err) {
          console.error('Error fetching workers:', err);
          setWorkers([]);
        }

        // Fetch services for this business
        try {
          const servicesResponse = await api.get(`/business/${id}/services`);
          setServices(servicesResponse.data || []);
        } catch (err) {
          console.error('Error fetching services:', err);
          setServices([]);
        }

      } catch (err) {
        console.error('Error fetching business data:', err);
        setError(language === 'en'
          ? 'Failed to load business details'
          : language === 'tr'
          ? 'letme detaylar yklenemedi'
          : '    '
        );
      } finally {
        setLoading(false);
      }
    };

    if (id) {
      fetchBusinessData();
    }
  }, [id, language]);

  const handleBooking = (service) => {
    if (!isAuthenticated) {
      navigate('/signin');
      return;
    }
    setSelectedService(service);
    // If only one worker, auto-select; otherwise user will choose
    if (service.workers && service.workers.length === 1) {
      setSelectedWorker(service.workers[0].id || '');
    } else {
      setSelectedWorker('');
    }
    setSelectedDate('');
    setSelectedTime('');
    setBookingSuccess(false);
    setBookingError('');
    setBookingDialogOpen(true);
  };

  const handleSubmitBooking = async () => {
    try {
      setBookingError('');

      if (!selectedWorker || !selectedDate || !selectedTime) {
        setBookingError(language === 'en'
          ? 'Please fill all fields'
          : language === 'tr'
          ? 'Ltfen tm alanlar doldurun'
          : ',   '
        );
        return;
      }

      // Combine date and time into ISO format
      const startTime = `${selectedDate}T${selectedTime}:00`;

      // Find the correct service_id for the selected worker
      const serviceForWorker = selectedService.allServices?.find(s => s.barber_id === parseInt(selectedWorker)) || selectedService;

      const appointmentData = {
        service_id: serviceForWorker.id,
        barber_id: parseInt(selectedWorker),
        start_time: startTime
      };

      await api.post('/bookings/', appointmentData);

      setBookingSuccess(true);
      setTimeout(() => {
        setBookingDialogOpen(false);
        navigate('/dashboard');
      }, 2000);

    } catch (err) {
      console.error('Booking error:', err);
      setBookingError(err.response?.data?.detail || (
        language === 'en'
          ? 'Failed to create appointment'
          : language === 'tr'
          ? 'Randevu oluturulamad'
          : '   '
      ));
    }
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '100vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  if (error || !business) {
    return (
      <Container sx={{ py: 4 }}>
        <Alert severity="error">{error || 'Business not found'}</Alert>
        <Button startIcon={<ArrowBack />} onClick={() => navigate('/')} sx={{ mt: 2 }}>
          {language === 'en' ? 'Back to Home' : language === 'tr' ? 'Ana Sayfaya Dn' : '  '}
        </Button>
      </Container>
    );
  }

  return (
    <Box sx={{ bgcolor: '#f8fffe', minHeight: '100vh', display: 'flex', flexDirection: 'column' }}>
      {/* Header with Logo and Language Selector */}
      <AppBar position="sticky" elevation={0} sx={{
        background: 'linear-gradient(135deg, #00a693 0%, #00d4bd 100%)',
        borderBottom: '1px solid #008577'
      }}>
        <Toolbar sx={{ px: { xs: 2, md: 4 } }}>
          <Box sx={{ flexGrow: 1 }}>
            <Logo size="small" variant="color" onClick={() => navigate('/')} />
          </Box>

          <FormControl size="small" sx={{ minWidth: 100 }}>
            <Select
              value={language}
              onChange={(e) => changeLanguage(e.target.value)}
              sx={{
                color: 'white',
                '& .MuiOutlinedInput-notchedOutline': { borderColor: 'rgba(255,255,255,0.3)' },
                '& .MuiSvgIcon-root': { color: 'white' }
              }}
            >
              <MenuItem value="en"> English</MenuItem>
              <MenuItem value="tr"> Trke</MenuItem>
              <MenuItem value="ru"> </MenuItem>
            </Select>
          </FormControl>
        </Toolbar>
      </AppBar>

      {/* Business Header */}
      <Box sx={{ position: 'relative', mb: 8 }}>
        {/* Cover Photo */}
        <Box
          sx={{
            height: { xs: 200, md: 280 },
            background: business.cover_photo
              ? `url(${business.cover_photo}) center/cover`
              : 'linear-gradient(135deg, #00a693 0%, #4fd5c7 100%)',
            position: 'relative',
            '&::after': {
              content: '""',
              position: 'absolute',
              bottom: 0,
              left: 0,
              right: 0,
              height: '50%',
              background: 'linear-gradient(to top, rgba(0,0,0,0.4), transparent)'
            }
          }}
        />

        <Container>
          <Box sx={{ position: 'relative', mt: -8 }}>
            <Card sx={{ p: 3 }}>
              <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, gap: 3, alignItems: { xs: 'center', md: 'flex-start' } }}>
                {/* Avatar */}
                <Avatar
                  src={business.avatar || ''}
                  sx={{
                    width: 120,
                    height: 120,
                    border: '4px solid white',
                    bgcolor: '#00a693',
                    fontSize: '3rem',
                    fontWeight: 'bold'
                  }}
                >
                  {business.business_name?.[0]?.toUpperCase() || 'B'}
                </Avatar>

                {/* Business Info */}
                <Box sx={{ flex: 1, textAlign: { xs: 'center', md: 'left' } }}>
                  <Typography variant="h3" sx={{ fontWeight: 'bold', mb: 1 }}>
                    {business.business_name}
                  </Typography>

                  {business.description && (
                    <Typography variant="body1" color="text.secondary" sx={{ mb: 2 }}>
                      {business.description}
                    </Typography>
                  )}

                  <Stack direction="row" spacing={2} sx={{ mb: 2, justifyContent: { xs: 'center', md: 'flex-start' } }}>
                    {business.address && (
                      <Chip icon={<LocationOn />} label={business.address} />
                    )}
                    {business.phone && (
                      <Chip icon={<Phone />} label={business.phone} />
                    )}
                    {business.email && (
                      <Chip icon={<Email />} label={business.email} />
                    )}
                  </Stack>

                  {/* Social Media */}
                  {(business.facebook || business.instagram) && (
                    <Stack direction="row" spacing={1} sx={{ justifyContent: { xs: 'center', md: 'flex-start' } }}>
                      {business.facebook && (
                        <IconButton
                          component="a"
                          href={business.facebook}
                          target="_blank"
                          sx={{ color: '#3b82f6' }}
                        >
                          <Facebook />
                        </IconButton>
                      )}
                      {business.instagram && (
                        <IconButton
                          component="a"
                          href={business.instagram}
                          target="_blank"
                          sx={{ color: '#e1306c' }}
                        >
                          <Instagram />
                        </IconButton>
                      )}
                    </Stack>
                  )}
                </Box>
              </Box>
            </Card>
          </Box>
        </Container>
      </Box>

      <Container>
        <Grid container spacing={4}>
          {/* Services Section */}
          <Grid item xs={12} md={8}>
            <Typography variant="h4" sx={{ fontWeight: 'bold', mb: 3 }}>
              {language === 'en' ? 'Services' : language === 'tr' ? 'Hizmetler' : ''}
            </Typography>

            {services.length === 0 ? (
              <Card>
                <CardContent>
                  <Typography color="text.secondary" textAlign="center">
                    {language === 'en' ? 'No services available' : language === 'tr' ? 'Henz hizmet yok' : ' '}
                  </Typography>
                </CardContent>
              </Card>
            ) : (
              <Grid container spacing={2}>
                {(() => {
                  // Group services by name
                  const groupedServices = services.reduce((acc, service) => {
                    if (!acc[service.name]) {
                      acc[service.name] = [];
                    }
                    acc[service.name].push(service);
                    return acc;
                  }, {});

                  return Object.entries(groupedServices).map(([serviceName, serviceGroup]) => {
                    const firstService = serviceGroup[0];
                    const serviceWorkers = serviceGroup
                      .map(s => workers.find(w => w.id === s.barber_id))
                      .filter(Boolean);

                    // Determine icon based on service name/category
                    const getCategoryIcon = () => {
                      const name = serviceName.toLowerCase();
                      const desc = (firstService.description || '').toLowerCase();
                      const text = name + ' ' + desc;

                      if (text.includes('hair') || text.includes('barber') || text.includes('cut') || text.includes('sa') || text.includes('kuafr') || text.includes('gttt')) {
                        return <ContentCut sx={{ fontSize: 28, color: '#00a693' }} />;
                      } else if (text.includes('spa') || text.includes('massage') || text.includes('facial') || text.includes('beauty') || text.includes('gzellik')) {
                        return <Spa sx={{ fontSize: 28, color: '#00a693' }} />;
                      } else if (text.includes('fitness') || text.includes('gym') || text.includes('workout') || text.includes('spor')) {
                        return <FitnessCenter sx={{ fontSize: 28, color: '#00a693' }} />;
                      }
                      return <ContentCut sx={{ fontSize: 28, color: '#00a693' }} />; // Default to barber icon
                    };

                    return (
                      <Grid item xs={12} key={serviceName}>
                        <Card sx={{
                          transition: 'transform 0.2s',
                          '&:hover': { transform: 'translateY(-4px)', boxShadow: 3 }
                        }}>
                          <CardContent>
                            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 2 }}>
                              <Box sx={{ flex: 1 }}>
                                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1.5, mb: 1 }}>
                                  {getCategoryIcon()}
                                  <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                                    {serviceName}
                                  </Typography>
                                </Box>
                                {firstService.description && (
                                  <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
                                    {firstService.description}
                                  </Typography>
                                )}
                                <Stack direction="row" spacing={2} sx={{ mb: 1, flexWrap: 'wrap', gap: 1 }}>
                                  <Chip
                                    icon={<Schedule />}
                                    label={`${firstService.duration} ${language === 'en' ? 'min' : language === 'tr' ? 'dk' : ''}`}
                                    size="small"
                                  />
                                  <Chip
                                    label={`${firstService.price}`}
                                    size="small"
                                    color="primary"
                                    sx={{ fontWeight: 'bold' }}
                                  />
                                  {serviceWorkers.map((worker, idx) => (
                                    <Chip
                                      key={idx}
                                      icon={<Person />}
                                      label={worker.full_name || worker.email}
                                      size="small"
                                      sx={{ bgcolor: '#e0f2f1', color: '#00a693' }}
                                    />
                                  ))}
                                </Stack>
                              </Box>
                            <Stack direction="row" spacing={1}>
                              <Button
                                variant="outlined"
                                startIcon={<Favorite />}
                                sx={{
                                  borderColor: '#ff6b35',
                                  color: '#ff6b35',
                                  '&:hover': { borderColor: '#e55a2e', bgcolor: '#fff5f0' }
                                }}
                                onClick={() => {
                                  alert(language === 'en'
                                    ? 'Added to favorites!'
                                    : language === 'tr'
                                    ? 'Favorilere eklendi!'
                                    : '  !');
                                }}
                              >
                                {language === 'en' ? 'Favorite' : language === 'tr' ? 'Favori' : ' '}
                              </Button>
                              <Button
                                variant="contained"
                                sx={{
                                  bgcolor: '#00a693',
                                  '&:hover': { bgcolor: '#008c7a' }
                                }}
                                onClick={() => handleBooking({ ...firstService, workers: serviceWorkers, allServices: serviceGroup })}
                              >
                                {language === 'en' ? 'Book' : language === 'tr' ? 'Rezervasyon' : ''}
                              </Button>
                            </Stack>
                          </Box>
                        </CardContent>
                      </Card>
                    </Grid>
                    );
                  });
                })()}
              </Grid>
            )}
          </Grid>

          {/* Workers Sidebar */}
          <Grid item xs={12} md={4}>
            <Typography variant="h4" sx={{ fontWeight: 'bold', mb: 3 }}>
              {language === 'en' ? 'Our Team' : language === 'tr' ? 'Ekibimiz' : ' '}
            </Typography>

            {workers.length === 0 ? (
              <Card>
                <CardContent>
                  <Typography color="text.secondary" textAlign="center">
                    {language === 'en' ? 'No workers available' : language === 'tr' ? 'Henz alan yok' : ' '}
                  </Typography>
                </CardContent>
              </Card>
            ) : (
              <Stack spacing={2}>
                {workers.map((worker) => (
                  <Card key={worker.id}>
                    <CardContent>
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                        <Avatar
                          src={worker.avatar || ''}
                          sx={{ width: 56, height: 56, bgcolor: '#00a693' }}
                        >
                          <Person />
                        </Avatar>
                        <Box>
                          <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                            {worker.full_name || worker.name || worker.email}
                          </Typography>
                          {worker.specialization && (
                            <Typography variant="body2" color="text.secondary">
                              {worker.specialization}
                            </Typography>
                          )}
                        </Box>
                      </Box>
                    </CardContent>
                  </Card>
                ))}
              </Stack>
            )}
          </Grid>
        </Grid>
      </Container>

      {/* Booking Dialog */}
      <Dialog open={bookingDialogOpen} onClose={() => setBookingDialogOpen(false)} maxWidth="sm" fullWidth>
        <DialogTitle>
          {language === 'en' ? 'Book Appointment' : language === 'tr' ? 'Randevu Al' : ''}
        </DialogTitle>
        <DialogContent>
          {bookingSuccess ? (
            <Alert severity="success" sx={{ mt: 2 }}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <CheckCircle />
                <Typography>
                  {language === 'en'
                    ? 'Appointment booked successfully!'
                    : language === 'tr'
                    ? 'Randevu baaryla alnd!'
                    : '  !'}
                </Typography>
              </Box>
            </Alert>
          ) : (
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 2 }}>
              {bookingError && (
                <Alert severity="error">{bookingError}</Alert>
              )}

              <TextField
                label={language === 'en' ? 'Service' : language === 'tr' ? 'Hizmet' : ''}
                value={selectedService?.name || ''}
                disabled
                fullWidth
              />

              <TextField
                select
                label={language === 'en' ? 'Worker' : language === 'tr' ? 'alan' : ''}
                value={selectedWorker}
                onChange={(e) => setSelectedWorker(e.target.value)}
                fullWidth
                required
              >
                {(selectedService?.workers || []).map((worker) => (
                  <MenuItem key={worker.id} value={worker.id}>
                    {worker.full_name || worker.name || worker.email}
                  </MenuItem>
                ))}
              </TextField>

              <TextField
                type="date"
                label={language === 'en' ? 'Date' : language === 'tr' ? 'Tarih' : ''}
                value={selectedDate}
                onChange={(e) => setSelectedDate(e.target.value)}
                InputLabelProps={{ shrink: true }}
                inputProps={{ min: new Date().toISOString().split('T')[0] }}
                fullWidth
                required
              />

              <TextField
                type="time"
                label={language === 'en' ? 'Time' : language === 'tr' ? 'Saat' : ''}
                value={selectedTime}
                onChange={(e) => setSelectedTime(e.target.value)}
                InputLabelProps={{ shrink: true }}
                fullWidth
                required
              />
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setBookingDialogOpen(false)}>
            {language === 'en' ? 'Cancel' : language === 'tr' ? 'ptal' : ''}
          </Button>
          {!bookingSuccess && (
            <Button
              onClick={handleSubmitBooking}
              variant="contained"
              sx={{ bgcolor: '#00a693', '&:hover': { bgcolor: '#008c7a' } }}
            >
              {language === 'en' ? 'Confirm' : language === 'tr' ? 'Onayla' : ''}
            </Button>
          )}
        </DialogActions>
      </Dialog>

      <Footer />
    </Box>
  );
};

export default BusinessDetail;
