import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import Logo from '../components/Logo';
import { useLanguage } from '../contexts/LanguageContext';
import { getServicesCatalog } from '../data/servicesCatalog';
import api, { dashboardAPI, servicesAPI, bookingAPI } from '../services/api';
import { formatDate, formatTime, formatDateTime } from '../utils/dateUtils';
import {
  Box,
  Container,
  Typography,
  Card,
  CardContent,
  Button,
  Grid,
  Avatar,
  Chip,
  AppBar,
  Toolbar,
  IconButton,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Divider,
  Stack,
  FormControl,
  Select,
  MenuItem,
  useTheme,
  useMediaQuery,
  TextField,
  Tabs,
  Tab,
  Paper,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Badge
} from '@mui/material';
import {
   ArrowBack,
   Schedule,
   Star,
   History,
   Favorite,
   LocationOn,
   AccessTime,
   EuroSymbol,
   PhotoCamera,
   Edit,
   CalendarToday,
   TrendingUp,
   Person
 } from '@mui/icons-material';

const Dashboard = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const { user, updateUser } = useAuth();
  const { language, changeLanguage } = useLanguage();

  const [tabValue, setTabValue] = useState(() => {
    if (location.pathname === '/favorites') {
      return 1;
    }
    return location.state?.tab ?? 0;
  });
  const [photoUploadOpen, setPhotoUploadOpen] = useState(false);
  const [profileEditOpen, setProfileEditOpen] = useState(false);
  const [editFormData, setEditFormData] = useState({
    first_name: '',
    last_name: '',
    email: '',
    phone_number: ''
  });
  const [profilePhotoUrl, setProfilePhotoUrl] = useState('');

  const [upcomingAppointments, setUpcomingAppointments] = useState([]);
  const [pastAppointments, setPastAppointments] = useState([]);
  const [favoriteBarbers, setFavoriteBarbers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const [profileInfo, setProfileInfo] = useState(null);
  const [searchCity, setSearchCity] = useState('');
  const [availableServices, setAvailableServices] = useState([]);
  const [favoriteServices, setFavoriteServices] = useState([]);

  const content = {
    en: {
      dashboard: 'My Dashboard',
      upcomingAppointment: 'Upcoming',
      pastAppointment: 'Completed',
      favoriteServices: 'Favorites',
      confirmed: 'Confirmed',
      pending: 'Pending',
      completed: 'Completed',
      cancelled: 'Cancelled',
      givenRating: 'Given rating',
      writeReview: 'Write Review',
      visits: 'visits',
      bookAppointment: 'Book Appointment',
      phone: 'Phone',
      pastAppointmentsTitle: 'Past Appointments',
      search: 'Search',
      findServices: 'Find Services',
      viewBarberProfile: 'View Barber Profile',
      noPastAppointments: 'No past appointments',
      noFavoriteServices: 'No favorite services',
      loadingPast: 'Loading past appointments...',
      loadingFavorites: 'Loading favorite barbers...',
      failedPast: 'Failed to load past appointments',
      failedFavorites: 'Failed to load favorite barbers',
      retry: 'Retry',
      membership: 'Membership',
      userType: 'User Type',
      memberSince: 'Member Since',
      editProfile: 'Edit Profile',
      uploadPhoto: 'Upload Photo'
    },
    tr: {
      dashboard: 'Kontrol Panelim',
      upcomingAppointment: 'Yaklaan',
      pastAppointment: 'Tamamlanan',
      favoriteServices: 'Favoriler',
      confirmed: 'Onayland',
      pending: 'Beklemede',
      completed: 'Tamamland',
      cancelled: 'ptal Edildi',
      givenRating: 'Verilen puan',
      writeReview: 'Yorum Yaz',
      visits: 'ziyaret',
      bookAppointment: 'Randevu Al',
      phone: 'Telefon',
      pastAppointmentsTitle: 'Gemi Randevular',
      search: 'Ara',
      findServices: 'Servis Bul',
      viewBarberProfile: 'Profili Gr',
      noPastAppointments: 'Gemi randevu yok',
      noFavoriteServices: 'Favori servis yok',
      loadingPast: 'Gemi randevular ykleniyor...',
      loadingFavorites: 'Favori berberler ykleniyor...',
      failedPast: 'Gemi randevular yklenemedi',
      failedFavorites: 'Favori berberler yklenemedi',
      retry: 'Tekrar Dene',
      membership: 'yelik',
      userType: 'Kullanc Tipi',
      memberSince: 'ye Olma Tarihi',
      editProfile: 'Profili Dzenle',
      uploadPhoto: 'Fotoraf Ykle'
    },
    ru: {
      dashboard: ' ',
      upcomingAppointment: '',
      pastAppointment: '',
      favoriteServices: '',
      confirmed: '',
      pending: ' ',
      completed: '',
      cancelled: '',
      givenRating: '',
      writeReview: ' ',
      visits: '',
      bookAppointment: '',
      phone: '',
      pastAppointmentsTitle: ' ',
      search: '',
      findServices: ' ',
      viewBarberProfile: '',
      noPastAppointments: '  ',
      noFavoriteServices: ' ',
      loadingPast: '...',
      loadingFavorites: ' ...',
      failedPast: ' ',
      failedFavorites: '  ',
      retry: '',
      membership: '',
      userType: ' ',
      memberSince: ' ',
      editProfile: '',
      uploadPhoto: ' '
    }
  };

  const t = content[language];

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        // Fetch appointments using bookingAPI
        const appointmentsResponse = await bookingAPI.getAppointments().catch(() => []);

        // Transform appointments to display format
        const transformedAppointments = (appointmentsResponse || []).map(apt => {
          return {
            id: apt.id,
            barberName: 'Worker', // Will be fetched separately
            service: `Service #${apt.service_id}`, // Will be fetched separately
            date: formatDate(apt.start_time, language),
            time: formatTime(apt.start_time, language),
            status: apt.status,
            barberImage: null,
            start_time: apt.start_time
          };
        });

        // Separate upcoming and past appointments
        const now = new Date();
        const upcoming = transformedAppointments.filter(apt => new Date(apt.start_time) >= now);
        const past = transformedAppointments.filter(apt => new Date(apt.start_time) < now);

        setUpcomingAppointments(upcoming);
        setPastAppointments(past);
        setFavoriteServices([]);
        setError(null);
      } catch (err) {
        console.error('Error fetching dashboard data:', err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const getUserFullName = () => {
    if (user?.full_name) return user.full_name;
    if (user?.first_name && user?.last_name) {
      return `${user.first_name} ${user.last_name}`;
    }
    if (profileInfo?.first_name && profileInfo?.last_name) {
      return `${profileInfo.first_name} ${profileInfo.last_name}`;
    }
    return profileInfo?.email?.split('@')[0] || user?.email?.split('@')[0] || 'User';
  };

  const getUserInitials = () => {
    const fullName = getUserFullName();
    const names = fullName.split(' ');
    if (names.length >= 2) {
      return `${names[0][0]}${names[1][0]}`.toUpperCase();
    }
    return fullName.substring(0, 2).toUpperCase();
  };

  const getUserEmail = () => {
    return user?.email || profileInfo?.email || 'user@example.com';
  };

  const handlePhotoUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setProfilePhotoUrl(reader.result);
        setPhotoUploadOpen(false);
      };
      reader.readAsDataURL(file);
    }
  };

  function TabPanel({ children, value, index }) {
    return value === index ? <Box sx={{ p: 3 }}>{children}</Box> : null;
  }

  return (
    <Box sx={{ bgcolor: '#f0f4f8', minHeight: '100vh' }}>
      {/* Modern Header */}
      <AppBar position="sticky" elevation={0} sx={{
        background: 'linear-gradient(135deg, #00a693 0%, #00d4bd 100%)',
        borderBottom: '1px solid #008577'
      }}>
        <Toolbar sx={{ px: { xs: 2, md: 4 } }}>
          <Box sx={{ flexGrow: 1 }}>
            <Logo size="small" variant="white" />
          </Box>
        </Toolbar>
      </AppBar>

      <Container maxWidth="xl" sx={{ py: 4 }}>
        {/* Profile Card - Top */}
        <Card sx={{
          mb: 4,
          background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
          color: 'white',
          borderRadius: 4,
          boxShadow: '0 8px 32px rgba(102,126,234,0.3)'
        }}>
          <CardContent sx={{ p: 4 }}>
            <Grid container spacing={3} alignItems="center">
              <Grid item>
                <Badge
                  overlap="circular"
                  anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                  badgeContent={
                    <IconButton
                      sx={{
                        bgcolor: '#00a693',
                        color: 'white',
                        width: 40,
                        height: 40,
                        '&:hover': { bgcolor: '#008c7a' }
                      }}
                      onClick={() => setPhotoUploadOpen(true)}
                    >
                      <PhotoCamera />
                    </IconButton>
                  }
                >
                  <Avatar
                    src={profilePhotoUrl}
                    sx={{
                      width: 120,
                      height: 120,
                      border: '4px solid white',
                      bgcolor: 'rgba(255,255,255,0.3)',
                      fontSize: '3rem',
                      fontWeight: 'bold'
                    }}
                  >
                    {!profilePhotoUrl && getUserInitials()}
                  </Avatar>
                </Badge>
              </Grid>
              <Grid item xs>
                <Typography variant="h4" sx={{ fontWeight: 'bold', mb: 1 }}>
                  {getUserFullName()}
                </Typography>
                <Typography variant="h6" sx={{ opacity: 0.9, mb: 2 }}>
                  {getUserEmail()}
                </Typography>
                <Stack direction="row" spacing={2}>
                  <Chip
                    icon={<Person />}
                    label={profileInfo?.membership_tier || 'Standard Member'}
                    sx={{ bgcolor: 'rgba(255,255,255,0.2)', color: 'white', fontWeight: 'bold' }}
                  />
                  <Chip
                    icon={<CalendarToday />}
                    label={`Member since ${new Date().getFullYear()}`}
                    sx={{ bgcolor: 'rgba(255,255,255,0.2)', color: 'white', fontWeight: 'bold' }}
                  />
                </Stack>
              </Grid>
              <Grid item>
                <Button
                  variant="contained"
                  startIcon={<Edit />}
                  onClick={() => {
                    setEditFormData({
                      first_name: user?.first_name || '',
                      last_name: user?.last_name || '',
                      email: user?.email || '',
                      phone_number: user?.phone_number || ''
                    });
                    setProfileEditOpen(true);
                  }}
                  sx={{
                    bgcolor: 'white',
                    color: '#667eea',
                    fontWeight: 'bold',
                    px: 3,
                    py: 1.5,
                    '&:hover': { bgcolor: '#f0f0f0' }
                  }}
                >
                  {t.editProfile}
                </Button>
              </Grid>
            </Grid>
          </CardContent>
        </Card>

        {/* Stats Cards */}
        <Grid container spacing={3} sx={{ mb: 4 }}>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
              color: 'white',
              borderRadius: 3,
              transition: 'transform 0.3s',
              '&:hover': { transform: 'translateY(-5px)', boxShadow: '0 12px 40px rgba(102,126,234,0.4)' }
            }}>
              <CardContent sx={{ textAlign: 'center', py: 4 }}>
                <Schedule sx={{ fontSize: 50, mb: 2, opacity: 0.9 }} />
                <Typography variant="h3" sx={{ fontWeight: 'bold', mb: 1 }}>
                  {upcomingAppointments.length}
                </Typography>
                <Typography variant="body1" sx={{ opacity: 0.9 }}>
                  {t.upcomingAppointment}
                </Typography>
              </CardContent>
            </Card>
          </Grid>

          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{
              background: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)',
              color: 'white',
              borderRadius: 3,
              transition: 'transform 0.3s',
              '&:hover': { transform: 'translateY(-5px)', boxShadow: '0 12px 40px rgba(245,87,108,0.4)' }
            }}>
              <CardContent sx={{ textAlign: 'center', py: 4 }}>
                <History sx={{ fontSize: 50, mb: 2, opacity: 0.9 }} />
                <Typography variant="h3" sx={{ fontWeight: 'bold', mb: 1 }}>
                  {pastAppointments.length}
                </Typography>
                <Typography variant="body1" sx={{ opacity: 0.9 }}>
                  {t.pastAppointment}
                </Typography>
              </CardContent>
            </Card>
          </Grid>

          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{
              background: 'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)',
              color: 'white',
              borderRadius: 3,
              transition: 'transform 0.3s',
              '&:hover': { transform: 'translateY(-5px)', boxShadow: '0 12px 40px rgba(79,172,254,0.4)' }
            }}>
              <CardContent sx={{ textAlign: 'center', py: 4 }}>
                <Favorite sx={{ fontSize: 50, mb: 2, opacity: 0.9 }} />
                <Typography variant="h3" sx={{ fontWeight: 'bold', mb: 1 }}>
                  {favoriteServices.length}
                </Typography>
                <Typography variant="body1" sx={{ opacity: 0.9 }}>
                  {t.favoriteServices}
                </Typography>
              </CardContent>
            </Card>
          </Grid>

          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{
              background: 'linear-gradient(135deg, #43e97b 0%, #38f9d7 100%)',
              color: 'white',
              borderRadius: 3,
              transition: 'transform 0.3s',
              '&:hover': { transform: 'translateY(-5px)', boxShadow: '0 12px 40px rgba(67,233,123,0.4)' }
            }}>
              <CardContent sx={{ textAlign: 'center', py: 4 }}>
                <TrendingUp sx={{ fontSize: 50, mb: 2, opacity: 0.9 }} />
                <Typography variant="h3" sx={{ fontWeight: 'bold', mb: 1 }}>
                  {pastAppointments.length * 2}
                </Typography>
                <Typography variant="body1" sx={{ opacity: 0.9 }}>
                  Total Visits
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>

        {/* Tabs Section */}
        <Card sx={{ borderRadius: 3, boxShadow: '0 4px 24px rgba(0,0,0,0.08)' }}>
          <Box sx={{ borderBottom: 1, borderColor: 'divider', bgcolor: '#fafafa' }}>
            <Tabs
              value={tabValue}
              onChange={(e, newValue) => setTabValue(newValue)}
              variant={isMobile ? "scrollable" : "standard"}
              scrollButtons={isMobile ? "auto" : false}
              sx={{
                '& .MuiTab-root': {
                  fontSize: '1rem',
                  fontWeight: 600,
                  py: 2.5
                },
                '& .Mui-selected': {
                  color: '#00a693'
                }
              }}
            >
              <Tab label={language === 'en' ? ' My Appointments' : language === 'tr' ? ' Randevularm' : '  '} />
              <Tab label={language === 'en' ? ' Favorites' : language === 'tr' ? ' Favoriler' : ' '} />
              <Tab label={language === 'en' ? ' History' : language === 'tr' ? ' Gemi' : ' '} />
            </Tabs>
          </Box>

          {/* My Appointments Tab */}
          <TabPanel value={tabValue} index={0}>
            {upcomingAppointments.length > 0 ? (
              <List>
                {upcomingAppointments.map((appointment, index) => (
                  <React.Fragment key={appointment.id}>
                    <ListItem sx={{
                      px: 3,
                      py: 3,
                      borderRadius: 2,
                      mb: 2,
                      bgcolor: '#f8f9fa',
                      '&:hover': { bgcolor: '#e9ecef' }
                    }}>
                      <ListItemAvatar>
                        <Avatar
                          src={appointment.barberImage}
                          sx={{ width: 70, height: 70, border: '3px solid #00a693' }}
                        />
                      </ListItemAvatar>
                      <ListItemText
                        sx={{ ml: 2 }}
                        primary={
                          <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                            {appointment.barberName || 'Service'}
                          </Typography>
                        }
                        secondary={
                          <Stack spacing={1}>
                            <Typography variant="body1" sx={{ color: '#00a693', fontWeight: 600 }}>
                              {appointment.service}
                            </Typography>
                            <Box sx={{ display: 'flex', gap: 2 }}>
                              <Chip icon={<CalendarToday />} label={appointment.date} size="small" />
                              <Chip icon={<AccessTime />} label={appointment.time} size="small" />
                              <Chip
                                label={appointment.status || 'Confirmed'}
                                size="small"
                                sx={{
                                  bgcolor: '#e8f5e9',
                                  color: '#2e7d32',
                                  fontWeight: 'bold'
                                }}
                              />
                            </Box>
                          </Stack>
                        }
                      />
                    </ListItem>
                  </React.Fragment>
                ))}
              </List>
            ) : (
              <Box sx={{ textAlign: 'center', py: 8 }}>
                <Schedule sx={{ fontSize: 80, color: '#ccc', mb: 2 }} />
                <Typography variant="h6" color="text.secondary">
                  No upcoming appointments
                </Typography>
                <Button
                  variant="contained"
                  onClick={() => navigate('/')}
                  sx={{
                    mt: 3,
                    bgcolor: '#00a693',
                    '&:hover': { bgcolor: '#008c7a' },
                    px: 4,
                    py: 1.5
                  }}
                >
                  Book Now
                </Button>
              </Box>
            )}
          </TabPanel>

          {/* Favorites Tab */}
          <TabPanel value={tabValue} index={1}>
            {favoriteServices.length > 0 ? (
              <Grid container spacing={3}>
                {favoriteServices.map((service) => (
                  <Grid item xs={12} sm={6} md={4} key={service.id}>
                    <Card sx={{
                      borderRadius: 3,
                      transition: 'all 0.3s',
                      '&:hover': {
                        transform: 'translateY(-5px)',
                        boxShadow: '0 12px 40px rgba(0,0,0,0.15)'
                      }
                    }}>
                      <CardContent>
                        <Avatar
                          src={service.image}
                          sx={{ width: 60, height: 60, mb: 2, bgcolor: '#00a693' }}
                        >
                          {service.name?.[0]}
                        </Avatar>
                        <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                          {service.name}
                        </Typography>
                        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                          {service.description}
                        </Typography>
                        <Button
                          variant="outlined"
                          fullWidth
                          sx={{
                            color: '#00a693',
                            borderColor: '#00a693',
                            '&:hover': { bgcolor: '#e0f2f1', borderColor: '#00a693' }
                          }}
                        >
                          View Details
                        </Button>
                      </CardContent>
                    </Card>
                  </Grid>
                ))}
              </Grid>
            ) : (
              <Box sx={{ textAlign: 'center', py: 8 }}>
                <Favorite sx={{ fontSize: 80, color: '#ccc', mb: 2 }} />
                <Typography variant="h6" color="text.secondary">
                  {t.noFavoriteServices}
                </Typography>
              </Box>
            )}
          </TabPanel>

          {/* History Tab */}
          <TabPanel value={tabValue} index={2}>
            {pastAppointments.length > 0 ? (
              <List>
                {pastAppointments.map((appointment, index) => (
                  <React.Fragment key={appointment.id}>
                    <ListItem sx={{ px: 3, py: 3 }}>
                      <ListItemAvatar>
                        <Avatar src={appointment.barberImage} sx={{ width: 60, height: 60 }} />
                      </ListItemAvatar>
                      <ListItemText
                        sx={{ ml: 2 }}
                        primary={
                          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                            <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                              {appointment.barberName}
                            </Typography>
                            <Typography variant="h6" sx={{ color: '#00a693', fontWeight: 'bold' }}>
                              {appointment.price}
                            </Typography>
                          </Box>
                        }
                        secondary={
                          <Stack spacing={1}>
                            <Typography variant="body1">{appointment.service}</Typography>
                            <Typography variant="body2" color="text.secondary">
                              {appointment.date} - {appointment.time}
                            </Typography>
                            {appointment.rating && (
                              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                <Star sx={{ color: '#fbbf24' }} />
                                <Typography>{appointment.rating}/5</Typography>
                              </Box>
                            )}
                          </Stack>
                        }
                      />
                    </ListItem>
                    {index < pastAppointments.length - 1 && <Divider />}
                  </React.Fragment>
                ))}
              </List>
            ) : (
              <Box sx={{ textAlign: 'center', py: 8 }}>
                <History sx={{ fontSize: 80, color: '#ccc', mb: 2 }} />
                <Typography variant="h6" color="text.secondary">
                  {t.noPastAppointments}
                </Typography>
              </Box>
            )}
          </TabPanel>
        </Card>
      </Container>

      {/* Photo Upload Dialog */}
      <Dialog open={photoUploadOpen} onClose={() => setPhotoUploadOpen(false)} maxWidth="sm" fullWidth>
        <DialogTitle sx={{ bgcolor: '#e0f2f1', color: '#00a693', fontWeight: 'bold' }}>
           {t.uploadPhoto}
        </DialogTitle>
        <DialogContent sx={{ py: 4 }}>
          <Box sx={{ textAlign: 'center' }}>
            <input
              accept="image/*"
              style={{ display: 'none' }}
              id="photo-upload"
              type="file"
              onChange={handlePhotoUpload}
            />
            <label htmlFor="photo-upload">
              <Button
                variant="contained"
                component="span"
                startIcon={<PhotoCamera />}
                sx={{
                  bgcolor: '#00a693',
                  '&:hover': { bgcolor: '#008c7a' },
                  px: 4,
                  py: 2,
                  fontSize: '1.1rem'
                }}
              >
                Choose Photo
              </Button>
            </label>
          </Box>
        </DialogContent>
        <DialogActions sx={{ p: 3 }}>
          <Button onClick={() => setPhotoUploadOpen(false)}>Cancel</Button>
        </DialogActions>
      </Dialog>

      {/* Edit Profile Dialog */}
      <Dialog open={profileEditOpen} onClose={() => setProfileEditOpen(false)} maxWidth="sm" fullWidth>
        <DialogTitle sx={{ fontWeight: 'bold', bgcolor: '#667eea', color: 'white' }}>
          {t.editProfile}
        </DialogTitle>
        <DialogContent sx={{ pt: 3 }}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label={language === 'en' ? 'First Name' : language === 'tr' ? 'Ad' : ''}
                value={editFormData.first_name}
                onChange={(e) => setEditFormData({ ...editFormData, first_name: e.target.value })}
                sx={{ mt: 1 }}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label={language === 'en' ? 'Last Name' : language === 'tr' ? 'Soyad' : ''}
                value={editFormData.last_name}
                onChange={(e) => setEditFormData({ ...editFormData, last_name: e.target.value })}
                sx={{ mt: 1 }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label={language === 'en' ? 'Email' : language === 'tr' ? 'E-posta' : 'Email'}
                value={editFormData.email}
                onChange={(e) => setEditFormData({ ...editFormData, email: e.target.value })}
                disabled
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label={language === 'en' ? 'Phone Number' : language === 'tr' ? 'Telefon' : ''}
                value={editFormData.phone_number}
                onChange={(e) => setEditFormData({ ...editFormData, phone_number: e.target.value })}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions sx={{ p: 3 }}>
          <Button onClick={() => setProfileEditOpen(false)}>
            {language === 'en' ? 'Cancel' : language === 'tr' ? 'ptal' : ''}
          </Button>
          <Button
            variant="contained"
            onClick={async () => {
              try {
                // Update user in AuthContext and localStorage (this also calls API)
                await updateUser(editFormData);
                setProfileEditOpen(false);
                alert(language === 'en' ? 'Profile updated!' : language === 'tr' ? 'Profil gncellendi!' : ' !');
              } catch (err) {
                console.error('Profile update error:', err);
                alert(language === 'en' ? 'Update failed' : language === 'tr' ? 'Gncelleme baarsz' : '');
              }
            }}
            sx={{ bgcolor: '#667eea', '&:hover': { bgcolor: '#5568d3' } }}
          >
            {language === 'en' ? 'Save' : language === 'tr' ? 'Kaydet' : ''}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default Dashboard;
